module.exports = "/* === Remove input autofocus webkit === */\n\n*:focus {\n    outline: none;\n}\n\n/* === Form Typography === */\n\nbody {\n    font: 14px/21px \"Lucida Sans\", \"Lucida Grande\", \"Lucida Sans Unicode\", sans-serif;\n}\n\n.contact_form h2, .contact_form label {\n    font-family: Georgia, Times, \"Times New Roman\", serif;\n}\n\n.form_hint, .required_notification {\n    font-size: 11px;\n}\n\n/* === List Styles === */\n\n.contact_form ul {\n    width: 750px;\n    list-style-type: none;\n    list-style-position: outside;\n    margin: 0px;\n    padding: 0px;\n}\n\n.contact_form li {\n    padding: 12px;\n    border-bottom: 1px solid #eee;\n    position: relative;\n}\n\n.contact_form li:first-child, .contact_form li:last-child {\n    border-bottom: 1px solid #777;\n}\n\n/* === Form Header === */\n\n.contact_form h2 {\n    margin: 1;\n    display: inline;\n}\n\n.required_notification {\n    color: #d45252;\n    margin: 5px 0 0 0;\n    display: inline;\n}\n\n/* === Form Elements === */\n\n.contact_form label {\n    width: 150px;\n    margin-top: 3px;\n    display: inline-block;\n    padding: 3px;\n}\n\n.contact_form .label2 {\n    width: 190px;\n    margin-top: 3px;\n    display: inline-block;\n    padding: 3px;\n}\n\n.contact_form .label3 {\n    width: 320px;\n    margin-top: 3px;\n    padding: 3px;\n    display: all;\n    float: none;\n}\n\n.contact_form .label4 {\n    width: 500px;\n    margin-top: 3px;\n    padding: 3px;\n    display: all;\n    float: none;\n}\n\n.contact_form .input2 {\n    height: 20px;\n    width: 20px;\n    padding: 5px 8px;\n}\n\n.contact_form input {\n    height: 20px;\n    width: 220px;\n    padding: 5px 8px;\n}\n\n.contact_form textarea {\n    padding: 8px;\n    width: 300px;\n}\n\n.contact_form button {\n    margin-left: 156px;\n}\n\n/* form element visual styles */\n\n.contact_form input, .contact_form textarea {\n    border: 1px solid #aaa;\n    box-shadow: 0px 0px 3px #ccc, 0 10px 15px #eee inset;\n    border-radius: 2px;\n    padding-right: 30px;\n    transition: padding .25s;\n}\n\n.contact_form input:focus, .contact_form textarea:focus {\n    background: #fff;\n    border: 1px solid #555;\n    box-shadow: 0 0 3px #aaa;\n    padding-right: 70px;\n}\n\n/* === HTML5 validation styles === */\n\n.contact_form input:required, .contact_form textarea:required {\n    background: #fff url(/assets/red_asterisk.png) no-repeat 98% center;\n}\n\n.contact_form input:required:valid, .contact_form textarea:required:valid {\n    background: #fff url(/assets/valid.png) no-repeat 98% center;\n    box-shadow: 0 0 5px #5cd053;\n    border-color: #28921f;\n}\n\n.contact_form input:focus:invalid, .contact_form textarea:focus:invalid {\n    background: #fff url(/assets/invalid.png) no-repeat 98% center;\n    box-shadow: 0 0 5px #d45252;\n    border-color: #b03535;\n}\n\n/* === Form hints === */\n\n.form_hint {\n    background: #d45252;\n    border-radius: 3px 3px 3px 3px;\n    color: white;\n    margin-left: 8px;\n    padding: 1px 6px;\n    z-index: 999;\n    /* hints stay above all other elements */\n    position: absolute;\n    /* allows proper formatting if hint is two lines */\n    display: none;\n}\n\n.form_hint::before {\n    content: \"\\25C0\";\n    color: #d45252;\n    position: absolute;\n    top: 1px;\n    left: -6px;\n}\n\n.contact_form input:focus+.form_hint {\n    display: inline;\n}\n\n.contact_form input:required:valid+.form_hint {\n    background: #28921f;\n}\n\n.contact_form input:required:valid+.form_hint::before {\n    color: #28921f;\n}\n\n/* === Button Style === */\n\nbutton.submit {\n    background-color: #68b12f;\n    background: -o-linear-gradient(top, #68b12f, #50911e);\n    border: 1px solid #509111;\n    border-bottom: 1px solid #5b992b;\n    border-radius: 3px;\n    -webkit-border-radius: 3px;\n    -moz-border-radius: 3px;\n    -ms-border-radius: 3px;\n    -o-border-radius: 3px;\n    box-shadow: inset 0 1px 0 0 #9fd574;\n    -webkit-box-shadow: 0 1px 0 0 #9fd574 inset;\n    -moz-box-shadow: 0 1px 0 0 #9fd574 inset;\n    -ms-box-shadow: 0 1px 0 0 #9fd574 inset;\n    -o-box-shadow: 0 1px 0 0 #9fd574 inset;\n    color: white;\n    font-weight: bold;\n    padding: 6px 20px;\n    text-align: center;\n    text-shadow: 0 -1px 0 #396715;\n}\n\nbutton.submit:hover {\n    opacity: .85;\n    cursor: pointer;\n}\n\nbutton.submit:active {\n    border: 1px solid #20911e;\n    box-shadow: 0 0 10px 5px #356b0b inset;\n    -webkit-box-shadow: 0 0 10px 5px #356b0b inset;\n    -moz-box-shadow: 0 0 10px 5px #356b0b inset;\n    -ms-box-shadow: 0 0 10px 5px #356b0b inset;\n    -o-box-shadow: 0 0 10px 5px #356b0b inset;\n}"